import React from "react";
import { ThemeProvider } from "@mui/material";
import { Site } from "./Site/Site";
import {
  CChart,
  useChartState,
  iEMA,
  iKAMA,
  createIRSI,
  Types as T,
  muiTheme,
  muiDarkTheme,
  defaultDarkTheme,
  defaultLightTheme,
} from "react-tech-chart";
import { fbExampleData } from "./dataseriesFb2021";
import { Layout } from "./Site/Layout";

export type Price = {
  date: string;
  open: number;
  high: number;
  low: number;
  close: number;
  volume: number;
};

const ExampleChartData = {
  name: "Meta Platforms",
  data: fbExampleData.map((dataset) => ({ ...dataset, date: new Date(dataset.date) })),
  RtData: undefined,
  type: "chart" as const,
  id: "mainchart-fb2021",
};

const Data: T.InputData[] = [
  ExampleChartData,
  {
    id: "kama_01",
    type: "indicator",
    indicator: iKAMA,
    graphProps: {
      style: {
        strokeColor: ["#0693E3"],
      },
    },
  },

  { type: "indicator", indicator: createIRSI({ period: 5 }), id: "rsi_01" },
  {
    id: "ema_rsi_01",
    type: "indicator",
    indSrcId: "rsi_01",
    indicator: iEMA,
    graphProps: {
      style: {
        strokeColor: ["#0693E3"],
      },
    },
  },
];

const settings: T.CChartProps["settings"] = {
  // disablePointerEvents: true,
  disableTheme: false,
  // initialState: defaultDarkTheme,
  initWidthPerTick: 8,
  // additionalIndicators: [iBuyStop, iSellStop],
};

function App() {
  const ContainerRef = React.useRef(null);
  const [DarkMode, setDarkMode] = React.useState(true);
  if (process.env.NODE_ENV === "production") {
    console.log("production mode");
    console.log = function () {};
  }

  const [ChartState, Dispatch, rtTicks] = useChartState({
    ContainerRef,
    data: Data,
    settings,
  });

  const toggleDark = React.useCallback(() => {
    Dispatch({ task: "setTheme", params: { theme: DarkMode ? defaultLightTheme : defaultDarkTheme } });
    setDarkMode((current) => !current);
  }, [Dispatch, DarkMode]);

  return (
    <React.Fragment>
      {/* <div style={{ position: "relative", top: 0, height: "100%" }}>
        <CChart chartData={[{ data: Data, name: "coolChart" }]} viewMode="fullscreen" style="TODO" />
      </div> */}
      <ThemeProvider theme={true ? muiDarkTheme : muiTheme}>
        
          <Layout setDarkMode={toggleDark}>
            <Site setDarkMode={toggleDark}>
              
                {/* <Test/> */}
                <CChart
                  ref={ContainerRef}
                  ChartState={ChartState}
                  Dispatch={Dispatch}
                  rtTicks={rtTicks}
                  settings={settings}
                  // darkMode={false}
                />
              
            </Site>
          </Layout>
       
      </ThemeProvider>
    </React.Fragment>
  );
}

export default App;
