import { mdiThemeLightDark, mdiGithub, mdiMenu } from "@mdi/js";
import { Toolbar, Typography, Button, IconButton, useTheme, Box, Stack } from "@mui/material";
import { Icon } from "@mdi/react";
import React from "react";
import { styled } from "@mui/system";

export const MenuIconButton = styled(IconButton)(({ theme }) => ({
  color: theme.palette.primary.main,
}));

export type LayoutProps = { setDarkMode: (current: any) => any };
export const Layout: React.FC<LayoutProps> = (props) => {
  const { setDarkMode } = props;
  const theme = useTheme();
  return (
    <Stack id="layoutRoot" sx={{ height: "100%" }}>
      <Box sx={{ height: 64, bgcolor: theme.palette.background.paper, boxShadow: "10px 10px" }}>
        <Toolbar sx={{ pr: 1 }}>
          <Typography variant="h6" sx={{ flexGrow: 1 }} color="primary">
            <a href="/">react-tech-chart</a>
          </Typography>
          <Button sx={{ color: theme.palette.primary.main }}>
            <Typography>Docs</Typography>
          </Button>
          <MenuIconButton
            //   sx={{ color: theme.palette.primary.main }}
            aria-label="menu"
            onClick={() => setDarkMode((current: any) => !current)}
          >
            <Icon path={mdiThemeLightDark} size={1.2} />
          </MenuIconButton>
          <MenuIconButton edge="start" aria-label="menu">
            <Icon path={mdiGithub} size={1.2} />
          </MenuIconButton>
          <MenuIconButton edge="start" aria-label="menu">
            <Icon path={mdiMenu} size={1.2} />
          </MenuIconButton>
        </Toolbar>
      </Box>
      {props.children}
    </Stack>
  );
};
